<template>
  <div class="chat-app">
    <div class="parent-container">
      <div v-if="!joined" class="container">
        <h1>ChatApp</h1>
        <input type="text" placeholder="Enter UserName" class="text-input" v-model="currentUser">
        <button class="btn" @click="join">Join</button>
      </div>
      <div v-if="joined" class="chat-container">
        <div class="message-list">
          <div v-for="message in messages" :key="message.id" :class="{ 'message': message, 'hero': message.user === currentUser }">
            <b>{{ message.user }}</b> : {{ message.text }}
          </div>
        </div>
        <div class="bottom">
          <textarea v-model="text" @keyup.enter="sendMessage" class="textarea" placeholder="Send Message"></textarea>
          <button class="send" @click="sendMessage">Send</button>
        </div>
      </div>   
    </div>
    <div class="user-list" v-if="joined"> <!-- Only display user list if joined -->
      <div v-for="(user, index) in availableUsers" :key="index">
        <input type="checkbox" :value="user" v-model="selectedUsers">
        <label>{{ user }}</label>
      </div>
    </div>
  </div>
</template>

<script>
import io from 'socket.io-client';

export default {
  name: 'ChatApp',
  data() {
    return {
      joined: false,
      currentUser: '',
      text: '',
      messages: [],
      availableUsers: [], // List of available users
      selectedUsers: [] // List of selected users for private chat
    };
  },
  methods: {
    join() {
      this.joined = true;
      this.socketInstance = io("https://8e05-2405-201-200b-d34f-59ba-5bc9-5d93-8f51.ngrok-free.app");

      this.socketInstance.on('message:received', (data) => {
        this.messages = this.messages.concat(data);
      });

      // Retrieve list of available users from the server
      this.socketInstance.on('userList', (users) => {
        this.availableUsers = users.filter(user => user !== this.currentUser);
      });

      // Notify the server about the current user
      this.socketInstance.emit('join', this.currentUser);
    },

    sendMessage() {
  const msg = {
    id: new Date().getTime(),
    text: this.text,
    user: this.currentUser,
    recipients: this.selectedUsers // Ensure selectedUsers array contains usernames
  };

  this.text = "";
  this.messages.push(msg);

  this.socketInstance.emit('privateMessage', msg); // Emit private message
}

  }
};
</script>

